"use client";

import React, { useState } from "react";
import { ArrowRight, ChevronDown } from "lucide-react";
import { formatDollar } from "@/shared/utils/currency.util";

const WalletPage = () => {
  const [selectedCurrency, setSelectedCurrency] = useState("USD");

  const walletBalance = 1750.0;

  return (
    <div className="border border-gray-200 rounded-2xl bg-white">
      {/* Page Title */}
      <h1 className="text-2xl font-semibold text-gray-900 p=4 mb-6 border-b border-gray-200">Wallet</h1>

      {/* Wallet Balance Card */}
      <div className="bg-white border border-gray-200 rounded-2xl p-8">
        <div className="mb-6">
          <h2 className="text-base font-semibold text-gray-900 mb-4">
            Wallet Balance
          </h2>

          {/* Balance Display */}
          <div className="flex items-baseline gap-2 mb-1">
            <span className="text-5xl font-bold text-gray-900">
              {formatDollar(walletBalance)}
            </span>
            <button className="flex items-center gap-1 text-sm text-gray-600 hover:text-gray-900 transition-colors">
              <span>{selectedCurrency}</span>
              <ChevronDownIcon className="w-4 h-4" />
            </button>
          </div>

          <p className="text-sm text-gray-600">Available balance</p>
        </div>

        {/* Top Up Button */}
        <button className="w-full bg-emerald-900 hover:bg-emerald-950 text-white py-3 px-6 rounded-lg font-medium flex items-center justify-center gap-2 transition-colors">
          <span>Top Up Wallet</span>
          <ArrowRight className="w-5 h-5" />
        </button>
      </div>
    </div>
  );
};

export default WalletPage;
